import React from 'react';
import {Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, Button} from 'reactstrap';

const ComicDisplay = (props) => {

    console.log(props.comicResults);

    const comicDisplayGrid = () => {
        return props.comicResults.map((comic, ind) => {
            const p = comic.thumbnail.path;
            const e = comic.thumbnail.extension;
            let t;

            if(p.startsWith('https')){
                t = p
            } else {
                t = p.replace('http', 'https')
            }

            console.log(t);

            return (
                  <Card key={ind} className="card">
                    <CardImg top width="100%" src={`${p}/standard_large.${e}`} alt="Image Not Available" />
                    <CardBody>
                      <CardTitle>{comic.title}</CardTitle>
                      <CardSubtitle>Card subtitle</CardSubtitle>
                      <CardText>Some quick example text to build on the card title and make up the bulk of the card's content.</CardText>
                      <Button>Button</Button>
                    </CardBody>
                  </Card>
              );
        })
    }
    
    return(
        <div className="container">
            {comicDisplayGrid()}
        </div>
    )
}

export default ComicDisplay;