import React, {useState} from 'react';
import {Form, FormGroup, Label, Input, Button} from 'reactstrap';

import ComicResults from './ComicResults';

const ComicSearch = () => {
    const [comicName, setComicName] = useState('');
    const [comicResults, setComicResults] = useState(false);

    const comicResultsOn = (e) => {
        e.preventDefault();
        setComicResults(true);
    }

    return(
        <div>
            <h1>Search For a Marvel Comic Series</h1>
            <Form onSubmit={comicResultsOn}>
                <FormGroup>
                    <Label htmlFor='comicName'/>
                    <Input name='comicName' value={comicName} onChange={(e) => setComicName(e.target.value)} />
                    <Button type='submit'>Search</Button>
                </FormGroup>
            </Form>
            {comicResults ? <ComicResults comicName={comicName} /> : <></>}
        </div>
    )
}

export default ComicSearch;